* {
    margin: 0px;
    padding: 0px;
    font-family: 'RIDIBatang','Youth','JejuGothic', "맑은 고딕";
    border-spacing: 0px;
}

html{
    width: 1700px;
}

@font-face {
    font-family: Youth;
    src: url("../fonts/Youth.ttf") format('truetype');
    font-weight: normal;
}

@font-face {
    font-family: JejuGothic;
    src: url("../fonts/JejuGothic.ttf") format('truetype');
    font-weight: normal;
}

@font-face {
    font-family: 'RIDIBatang';
    src: url('../fonts/RIDIBatang.otf') format('opentype');
    font-weight: normal;
    font-style: normal;
}

/*** Layout Start ***/

/** Top_Navigation **/
.top_nav{
    width: 100%;
    height: 250px;
}

/* 프로필 */
.top_nav .profile_box{
    width: 500px;
    height: 200px;
    float: left;
}

.top_nav .profile{
    width: 100px;
    height: 100px;
    border-radius: 100%;
    box-shadow:  0px 0px 6px 1px #aaa;
    overflow: hidden;

    // 위치
    margin-top: 50px;
    float: right;
}  

.top_nav .profile img{
    width: 100%;
    height: 100%;
}

/* 설명 */
.top_nav .description_box{
    width: auto;
    height: 200px;
}

.top_nav .description{
    width: 700px;
    height: 100px;

    // 위치
    margin-left: 100px;
    margin-top: 50px;
    float: left;
}

.top_nav .description div{
    margin-bottom: 15px;
}

/* 상단메뉴 */
.top_nav .menu{
    width: 900px;
    height: 50px;
    border-bottom: 0.5px #ddd solid;
    overflow: hidden;
    transition-duration: 0.5s;
    cursor: pointer;

    // 위치
    margin-left: 400px;
}

.top_nav .menu table{
    width: 100%;
    table-layout: fixed;

    -webkit-user-select:none; 
    -moz-user-select:none; 
    -ms-user-select:none; 
    user-select:none
}

.top_nav .menu th, .top_nav .menu td{
    height: 50px;
}

.top_nav .menu td:hover {
    background-color: #efefef;
}

.top_nav .menu td:active {
    background-color: #eaeaea;
    box-shadow: 0px 0px 5px inset #ccc;
}

.top_nav .menu tbody{
    height: 0px;
    text-align: center;
}

/** Left_Navigation **/
.left_nav{
    width: 400px;
    height: 500px;
    
    // 위치
    float: left;
}

/** Right_Navigation **/
.right_nav{
    width: 400px;
    height: 500px;

    // 위치
    float: left;
}

/** Content **/
.content{
    width: 900px;
    min-height: 500px;

    // 위치
    float: left;
}

/** Footer **/
.footer{
    width: 100%;
    height: 50px;

    // 위치
    position: fixed;
    bottom: 0px;
}

/** Index **/
.index{
    width: 100%;
    min-height: 800px;
    table-layout: fixed;

    border: solid 1px #ddd;
    margin-top: 50px;
}

.index .layout{
    height: 50%;
}
/*** Layout End ***/